@import url(https://fonts.googleapis.com/css?family=Varela+Round);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/**
 * Slides Custom CSS
 */

.opacity:not(.active) {
    opacity: 0;
}

.staffs:not(.active) {
  opacity: 0.01;
}

.circle#title,
.circle {
    background: #ffeb3b;
    line-height: 1.1;
    color: black;
    padding: 10rem  5rem;
    border-radius: 4% 50% 0% 70%;
    box-shadow: rgb(191 197 202) 10px 10px, rgba(0,0,0,0.3) 30px 30px;
}

#starter {
  background: black;
  color: hsla(0, 0%, 0%, .9);
  left: 0;
  letter-spacing: 5px;
  position: absolute;
  text-align: center;
  font: normal 140px Varela Round, sans-serif;
  text-transform: uppercase;
  -webkit-animation: move linear 2000ms infinite;
          animation: move linear 2000ms infinite;
}

#starter p {
  margin-top: 14rem;
}

.impress-enabled .step .btn-block {
  color: hsla(0,0%,100%,.8);
  text-decoration: none;
  padding: 0 .4em;
  background: #3e4852;
  border-radius: .2em;
  transition: all .5s;
}

.impress-enabled .step:not(.active) {
  z-index: -1;
}

.impress-enabled .step.active {
  z-index: 9999;
}

.step h2 {
  line-height: 1.2;
}

.step h2 > strong {
  font-size: 70%;
}

.step.large-title h2 {
  font-size: 10rem;
  font-weight: bold;
}

@-webkit-keyframes move {
  0% {
    text-shadow:
      0.1rem -0.1rem 0 hsla(0, 100%, 50%, 1),
      3px -3px 0 hsla(0, 100%, 50%, 1),
      2px -2px 0 hsla(0, 100%, 50%, 1),
      1px -1px 0 hsla(0, 100%, 50%, 1),
      -0.1rem 0.1rem 0 hsla(180, 100%, 50%, 1),
      -3px 3px 0 hsla(180, 100%, 50%, 1),
      -2px 2px 0 hsla(180, 100%, 50%, 1),
      -1px 1px 0 hsla(180, 100%, 50%, 1)
    ;
  }
  25% {
    text-shadow:
      -0.1rem -0.1rem 0 hsla(180, 100%, 50%, 1),
      -3px -3px 0 hsla(180, 100%, 50%, 1),
      -2px -2px 0 hsla(180, 100%, 50%, 1),
      -1px -1px 0 hsla(180, 100%, 50%, 1),
      0.1rem 0.1rem 0 hsla(0, 100%, 50%, 1),
      3px 3px 0 hsla(0, 100%, 50%, 1),
      2px 2px 0 hsla(0, 100%, 50%, 1),
      1px 1px 0 hsla(0, 100%, 50%, 1)
    ;
  }
  50% {
    text-shadow:
      -0.1rem 0.1rem 0 hsla(0, 100%, 50%, 1),
      -3px 3px 0 hsla(0, 100%, 50%, 1),
      -2px 2px 0 hsla(0, 100%, 50%, 1),
      -1px 1px 0 hsla(0, 100%, 50%, 1),
      0.1rem -0.1rem 0 hsla(180, 100%, 50%, 1),
      3px -3px 0 hsla(180, 100%, 50%, 1),
      2px -2px 0 hsla(180, 100%, 50%, 1),
      1px -1px 0 hsla(180, 100%, 50%, 1)
    ;
  }
  75% {
    text-shadow:
      0.1rem 0.1rem 0 hsla(180, 100%, 50%, 1),
      3px 3px 0 hsla(180, 100%, 50%, 1),
      2px 2px 0 hsla(180, 100%, 50%, 1),
      1px 1px 0 hsla(180, 100%, 50%, 1),
      -0.1rem -0.1rem 0 hsla(0, 100%, 50%, 1),
      -3px -3px 0 hsla(0, 100%, 50%, 1),
      -2px -2px 0 hsla(0, 100%, 50%, 1),
      -1px -1px 0 hsla(0, 100%, 50%, 1)
    ;
  }
  100% {
    text-shadow:
      0.1rem -0.1rem 0 hsla(0, 100%, 50%, 1),
      3px -3px 0 hsla(0, 100%, 50%, 1),
      2px -2px 0 hsla(0, 100%, 50%, 1),
      1px -1px 0 hsla(0, 100%, 50%, 1),
      -0.1rem 0.1rem 0 hsla(180, 100%, 50%, 1),
      -3px 3px 0 hsla(180, 100%, 50%, 1),
      -2px 2px 0 hsla(180, 100%, 50%, 1),
      -1px 1px 0 hsla(180, 100%, 50%, 1)
    ;
  }
}

